@mixin dir($dir) {
  [dir=#{$dir}] & {
    @content; } }

@mixin he {
  @include dir(rtl) {
    @content; } }

@mixin en {
  @include dir(ltr) {
    @content; } }

@mixin media($width) {
  @media (min-width: $width) {
    @content; } }

@mixin sm {
  @include media($sm) {
    @content; } }

@mixin md {
  @include media($md) {
    @content; } }

@mixin lg {
  @include media($lg) {
    @content; } }

@mixin xl {
  @include media($xl) {
    @content; } }
